$backg: #212121;

@mixin btn__hover {
    &:hover {
        background-image: none;
        background-color: blue; } }

.header {
    &__nav-menu {
        padding: 10px 71px 10px 71px;
        max-height: 53px;
        background: #212121; } }





.menu {
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;

    &__item {
        border-right: 2px dotted grey;
        padding-right: 44px;
        &:last-child {
            border: none; } }
    &__link {
        text-decoration: none;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: light;
        font-size: 15px;
        line-height: 15px;
        color: #ffff; } }

.subheader-m {
    margin-top: 17px; }

.subheader {
    position: absolute;
    width: 100%;
    min-height: 87px;
    background-color: rgba(0, 0,0 ,.6 );
    &__logo {
        max-width: 100%;
        margin-bottom: 6px;
        display: inline-block; }
    &__text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 100;
        font-size: 13px;
        color: #fff;
        opacity: 0.6; } }

.subheader-second {
    color: #fff;
    text-align: right;

    &__call {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        margin-top: 17px; }
    &__number {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        color: #fff;
        text-decoration: none; } }


.button {
    margin-top: 12px;
    width: 172px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    text-transform: uppercase;
    @include btn__hover; }




.first {
    text-align: center;
    max-height: 800px;
    background: url('../img/first/uber_bg.png') center center/cover no-repeat;
    color: white;
    padding-bottom: 35px;
    padding-top: 100px;


    &__title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 900;
        font-size: 60px;
        text-transform: uppercase;
        margin-top: 64px; }
    &__subtitle {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 36px;
        text-transform: uppercase;
        margin-bottom: 20px; }
    &__text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0; } }
    &__btn {
        margin: 115px auto 100px auto; } }






.about {
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    font-family: 'Roboto';
    font-style: normal;
    text-align: center; }

.causes {
    padding: 18px 67px;
    min-height: 802px;
    &__text {
        margin-bottom: 18px; }
    &__about {
        margin-bottom: 33px; } }

.main-title {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 38px;
        text-transform: uppercase;
        text-align: center; }

.causes-icons {
    display: flex;
    margin-bottom: 98px;
    &__rectangle {
        min-width: 114px;
        min-height: 114px;
        background: #34c0eb;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100px;
        margin-right: 25px; }
    &__item {
        text-align: center; }
    &__text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        max-height: 114px;
        min-width: 371px;
        span {
            color: #1EACC7;
            font-weight: bold; } } }

.mobile {
    min-height: 867px;
    background: url('../img/mobile/mobile_bg.png') center center/cover no-repeat;
    padding: 48px 22px 80px 22px;
    &__text {
        text-align: center;
        margin-bottom: 30px; }
    &__block {
        height: 280px;
        width: 320px;
        background: url('../img/mobile/Карточка 1.png');
        margin: 0 auto;
        padding-left: 18px;
        padding-right: 18px; } }

.mobile-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-bottom: 20px;
    &__icon {
        width: 29px;
        height: 29px;
        background: $backg;
        border-radius: 100px;
        text-align: center;
        position: absolute;
        bottom: 35px;
        &:before {
            display: block;
            width: 14px;
            height: 2px;
            background: blue; }


        img {
            width: 50%; } }
    &__text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 31px;
        color: white;
        text-align: center; } }

.choice {
    max-height: 898px;
    &__blackback {
        background: black;
        height: 449px;
        width: 100%; }
    &__grid {
        height: 449px;
        background: url('../img/mobile/Подложка.png'); } }

.choice {
    position: relative;
    &__blackback {
        padding-top: 59px; }
    &__auto {
        position: absolute;
        top: 26%;
        left: 26%; } }

.causes__about--white {
    background-color: white;
    color: black; }
.causes__text--white {
    color: white; }

.mobile__text--choice {
    color: white; }





.condition {
    min-height: 832px;
    padding-top: 28px; }

.condition-column {
    display: flex;
    margin: 0 auto;
    justify-content: center;
    &__second {
        max-width: 508px; } }

.column {
    &__first {
        list-style: none;
        max-width: 549px;
        img {
            margin-right: 16px; } }
    &__subtext {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 300;
        font-size: 17px;
        color: #1CA8C3;
        text-align: center; }
    &__text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        line-height: 24px;
        color: #1CA8C3;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 20px; }
    &__text--red {
        color: red; }
    &__icon {
        display: flex;
        margin-bottom: 16px; } }
